import React, { Component } from "react";
import Link from "next/link";

class Navbar extends Component  {
    constructor(props) {
        super(props)
        this.props = props
    }

    render() {
        return (
            <nav>
                <ul>
                    <li><Link href="/"><a>Home</a></Link></li>
                    <li><Link href="/about"><a style={linkstyle}>About</a></Link></li>
                </ul>
            </nav>
        );
    }
}

export default Navbar;